import React from "react";
import Chart from "react-apexcharts";

export default function ScatterQuotesApexChartOptionsLazy({series = 
    [{
        name: "name",
        data: [ [5, 5], [-5, 5], [-5, -5], [5, -5], [1,1], [-2, -2]]
    }] 
}) 
    {
    const scatterQuotesApexChartOptions =
    {
        chart: {
            height: 350,
            type: 'scatter',
            zoom: {
                enabled: true,
                type: 'xy'
            }
        },
        xaxis: {
            tickAmount: 10,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            }
        },
        yaxis: {
            tickAmount: 7
        }
    }

    return <Chart options={scatterQuotesApexChartOptions} series={[{ name: "period", data: series }]} type="scatter" height={350} />
}
